<template>
  <div class="">
    <ul>
      <li>
        <Hospital
          v-for="(data, index) in sortedRankingData"
          v-bind:key="data.name"
          v-bind:index="index + 1"
          v-bind:sector="data.sector"
          v-bind:state="data.state"
          v-bind:name="data.name"
          v-bind:stars="data.stars"
          v-bind:reviews="data.reviews"
        />
      </li>
    </ul>
  </div>
</template>

<script>
import Hospital from "./Hospital.vue";

export default {
  name: "Hospitals",
  components: {
    Hospital,
  },
  props: {
    rankingData: Array,
  },
  computed: {
    sortedRankingData: function () {
      return [...this.rankingData].sort(function (a, b) {
        if (a.stars === b.stars) {
          return parseFloat(b.reviews) - parseFloat(a.reviews);
        }
        return parseFloat(b.stars) - parseFloat(a.stars);
      });
    },
  },
};
</script>

<style scoped>
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  padding: 0;
  /* margin: 0 10px; */
}
</style>
